<template>
  <div class="about-block">
    <div :class="'about-block__img--' + textPosition">
      <img :src="image" alt="">
    </div>
    <div :class="'about-block__wave--' + textPosition">
      <img :src="require('@/assets/img/main/upwave.png')" alt="">
    </div>
    <div class="about-block__wrapper">
      <div class="container">
        <div :class="'about-block__content--' + textPosition">
          <h3 class="about-block__header">
            {{header}}
          </h3>
          <div class="about-block__text">
            {{text}}
          </div>
          <register-btn
            :name="btnName"
            :link="link"
          ></register-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import RegisterBtn from "@/components/RegisterBtn";

    export default {
        components: {RegisterBtn},
        props: ['header', 'text', 'image', 'btn-name', 'link', 'text-position'],
        name: "AboutTextBlock"
    }
</script>

<style lang="sass" scoped>
  .about-block
    width: 100%
    height: auto
    position: relative
    
    
    &__img
      width: 100%
      height: auto
      img
        position: relative
        width: 130%
        height: auto
        left: -15%
      
      &--left
        @extend .about-block__img
        background: #307ff4
        
        &:before
          content: ''
          display: block
          width: 60%
          height: 100%
          position: absolute
          top: 0
          left: 0
          background-image: linear-gradient(90deg, rgb(17, 110, 231) 0%, rgba(33, 152, 228, 0) 100%)
          z-index: 1
        
        &:after
          content: ''
          display: block
          width: 100%
          height: 20%
          position: absolute
          bottom: -5%
          left: 0
          background-image: url("../../assets/img/main/upwave.png")
          background-size: 100%
          background-repeat: no-repeat
          z-index: 1
      
      &--right
        @extend .about-block__img
        background: #307ff4
        
        
        &--left
          @extend .about-block__img
          background: #307ff4
          
          &:before
            content: ''
            display: block
            width: 60%
            height: 100%
            position: absolute
            top: 0
            left: 0
            background-image: linear-gradient(90deg, rgb(17, 110, 231) 0%, rgba(33, 152, 228, 0) 100%)
            z-index: 1
      
      
      &--center
        @extend .about-block__img
        background: #307ff4
        clip-path: polygon(0% -10vw, 100% 0, 100% 89%, 75% 86%, 50% 89%, 23% 84%, 0 90%)
        
        img
          top: -7vw
          width: 110%
          left: -5%
        
        &:before
          content: ''
          display: block
          width: 100%
          height: 60%
          position: absolute
          bottom: 0
          left: 0
          background-image: linear-gradient(180deg, rgba(33, 152, 228, 0) 0%, rgb(17, 110, 231) 100%)
          z-index: 1
      
      
   
    &__wave
      display: block
      width: 100%
      height: 20%
      position: absolute
      z-index: 1
      
      img
        width: 100%
      
      &--left
        @extend .about-block__wave
        bottom: -5%
        left: 0
      
      &--right
        @extend .about-block__wave
        bottom: -5%
        left: 0
      
      &--center
        @extend .about-block__wave
        transform: rotate3d(100, 1, 0, 180deg)
        bottom: 6%
        left: 0
    
    &__wrapper
      width: 100%
      height: 100%
      position: absolute
      z-index: 1
      left: 0
      top: 0
      overflow: hidden
      
      .container
        height: 100%
    
    &__content
      width: 40%
      height: 100%
      text-align: left
      padding-top: 5%
      
      &--left
        @extend .about-block__content
        float: left
      
      &--right
        @extend .about-block__content
        float: right
      
      &--center
        @extend .about-block__content
        text-align: center
        padding-top: 20%
        margin: 0 auto
        width: 70%
        
        .btn
          display: inline-flex
    
    &__header
      color: $whiteText
      font-family: $mainFont
      font-size: rem(46)
      font-weight: 700
      line-height: rem(56)
      margin-bottom: rem(20)
    
    &__text
      color: $whiteText
      font-family: $mainFont
      font-size: rem(16)
      font-weight: 400
      line-height: rem(22)
      margin-bottom: rem(40)

</style>