<template>
  <div class="activation">
    <h1>Reset Password</h1>
    <div class="message">
      {{message}}
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import call from '@/services/callService';
import {mapMutations} from 'vuex';

export default {
  name: 'activation',
  data() {
    return {
      message: ''
    }
  },
  methods: {
    ...mapMutations(['UPDATE_AUTH_COMPONENT']),
    async checkResetPwdKey() {
      try {
        const result = await call.isResetPwdKeyAvailable(this.getKey);
        if (result.kwargs.isAvailable) {
          this.message = `Key is Available: ${result.kwargs.isAvailable}, Можно перенаправить для смены пароля`;
          this.UPDATE_AUTH_COMPONENT({name: 'create-pass', isShown: true});
          this.$notify({
            group: 'app',
            type:'success',
            text: this.message
          });
        } else {
          this.message = `Key is Available: ${result.kwargs.isAvailable}, Наверно вы попали сюда случано`;
          this.$router.push('/auth/reset-pass');
          this.$notify({
            group: 'app',
            type:'warn',
            text: this.message
          });
        }
      } catch (err) {
        this.message = err.kwargs.desc || JSON.stringify(err);
        this.$notify({
          group: 'app',
          type:'error',
          text: this.message
        });
      }
    }
  },
  computed: {
    getKey() {
      return this.$route.params.id;
    }
  },
  mounted() {
    this.checkResetPwdKey();
  }
}
</script>
<style lang="sass" scoped>

</style>
